.results {
    display: block;
    list-style-type: none;
    color: #fff;
}

.results > li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 15px 0;
}

.results > li > * {
    margin: 0 6px;
    width: 25%;
}

.results img {
    width: 12vw;
    max-width: 100%;
    height: 100%;
}

.results .notfound {
    font-style: italic;
    color: #999;
    height: 75px;
}

.results .tobirating {
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    width: 8vw;
    margin: 0 15px;
}

.results .tobirating-1 {-webkit-transform: rotate(90deg);-ms-transform: rotate(90deg);transform: rotate(90deg)}    
.results .tobirating-2 {-webkit-transform: rotate(60deg);-ms-transform: rotate(60deg);transform: rotate(60deg)}    
.results .tobirating-3 {-webkit-transform: rotate(30deg);-ms-transform: rotate(30deg);transform: rotate(30deg)}    
.results .tobirating-4 {-webkit-transform: rotate(0);-ms-transform: rotate(0);transform: rotate(0)}    

.ratings {
    text-align: left;
}


